import { Component, h, Prop } from '@stencil/core';

/**
 */
@Component({
    tag: 'limebb-mention',
    styleUrl: 'markdown.scss',
    shadow: true,
})
export class Mention {
    @Prop()
    public limetype: string;

    @Prop()
    public objectid: number;

    @Prop()
    public descriptive: string;

    public render() {
        return (
            <a href={`/client/object/${this.limetype}/${this.objectid}`}>
                @{this.descriptive}
            </a>
        );
    }
}
